/*
 * === Main theme stylesheet ===
 *
 * Contents:
 *
 * 1.0 - Reset
 * 2.0 - Basic definitions
 * 3.0 - Typography
 *     3.1 - Font imports
 *     3.2 - Headings
 *     3.2 - Text
 * 4.0 - Content
 */
/**
 * 1.0 - Reset
 *
 * Nicolas Gallagher and Jonathan Neal http://necolas.github.com/normalize.css/
 */
@import 'https://fonts.googleapis.com/css?family=Cairo|Eczar';
html {
  font-family: sans-serif;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
}
body,
ul {
  margin: 0;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
main,
menu,
nav,
section,
summary {
  display: block;
}
audio,
canvas,
progress,
video {
  display: inline-block;
  vertical-align: baseline;
}
audio:not([controls]) {
  display: none;
  height: 0;
}
[hidden],
template {
  display: none;
}
a {
  background-color: transparent;
  color: inherit;
  text-decoration: none;
}
abbr[title] {
  border-bottom: 1px dotted;
}
b,
strong {
  font-weight: 700;
}
dfn,
cite,
em,
i {
  font-style: italic;
}
small {
  font-size: 80%;
}
sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sup {
  top: -0.5em;
}
sub {
  bottom: -0.25em;
}
img {
  border: 0;
}
svg:not(:root) {
  overflow: hidden;
}
figure {
  margin: 0;
}
hr {
  box-sizing: content-box;
}
code,
kbd,
pre,
samp {
  font-size: 1em;
}
button,
input,
optgroup,
select,
textarea {
  color: inherit;
  font: inherit;
  margin: 0;
}
select {
  text-transform: none;
}
button {
  overflow: visible;
}
button,
input,
select,
textarea {
  max-width: 100%;
}
button,
input[type="button"],
input[type="reset"],
input[type="submit"] {
  -webkit-appearance: button;
  cursor: pointer;
}
button[disabled],
html input[disabled] {
  cursor: default;
  opacity: .5;
}
button::-moz-focus-inner,
input::-moz-focus-inner {
  border: 0;
  padding: 0;
}
input[type="checkbox"],
input[type="radio"] {
  box-sizing: border-box;
  margin-right: 0.4375em;
  padding: 0;
}
input[type="number"]::-webkit-inner-spin-button,
input[type="number"]::-webkit-outer-spin-button {
  height: auto;
}
input[type="search"] {
  -webkit-appearance: textfield;
}
input[type="search"]::-webkit-search-cancel-button,
input[type="search"]::-webkit-search-decoration {
  -webkit-appearance: none;
}
fieldset {
  border: 1px solid #d1d1d1;
  margin: 0 0 1.75em;
  min-width: inherit;
  padding: 0.875em;
}
fieldset > :last-child {
  margin-bottom: 0;
}
legend {
  border: 0;
  padding: 0;
}
textarea {
  overflow: auto;
  vertical-align: top;
}
optgroup {
  font-weight: bold;
}
html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}
/**
 * 2.0 - Basic definitions
 */
/**
 * 3.0 - Typography
 */
/**
 * 3.1 - Font imports
 */
/**
 * 3.2 - Headings
 */
h1,
h2,
h3,
h4,
h5,
h6 {
  clear: both;
  margin: 0;
  text-rendering: optimizeLegibility;
  font-family: 'Cairo', sans-serif;
}
h2 {
  font-size: 40px;
  color: #2b6d82;
}
h3 {
  font-size: 25px;
  padding: 5px;
}
/**
 * 3.3 - Text
 */
body,
button,
input,
select,
textarea {
  color: #222;
  font-family: 'Cairo', sans-serif;
  font-size: 16px;
  font-size: 1rem;
  line-height: 1.75;
}
p {
  margin: 0 0 .75em;
  padding: 0 10px;
}
a {
  color: #2b6d82;
  border-bottom: 1px solid #2b6d82;
  font-style: italic;
}
.highlight {
  color: #2b6d82;
  border-bottom: 1px solid #2b6d82;
}
.almost-black-bg {
  background-color: #222;
}
.white-text {
  color: white;
}
.white-bg {
  background-color: white;
}
.theme-text {
  color: #2b6d82;
}
.theme-dark-text {
  color: #234452;
}
.theme-light-text {
  color: #9acede;
}
.red {
  color: #ff6666;
}
.left {
  text-align: left;
}
.right {
  text-align: right;
}
.icon-small-line {
  line-height: 31px;
}
.justify {
  text-align: justify;
}
/* 3.4 - Elements */
.radius {
  border-radius: 50%;
}
.center {
  text-align: center;
}
.pointer {
  cursor: pointer;
}
.margin-top-20 {
  margin-top: 20px;
}
.margin-top-40 {
  margin-top: 40px;
}
.margin-gap-top {
  margin-top: 15%;
}
.margin-gap-bottom {
  margin-bottom: 15%;
}
.margin-left-10p {
  margin-left: 10%;
}
.float-right {
  float: right;
}
.float-left {
  float: left;
}
.border {
  border: 1px solid #f1f0f0;
}
.hidden {
  display: none;
}
.not-selectable {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.icon {
  width: 45px;
  height: auto;
}
.icon-small {
  width: 31px;
  height: auto;
}
.margin-right-10 {
  margin-right: 10px;
}
.margin-left-20 {
  margin-left: 20px;
}
.margin-left-10 {
  margin-left: 10px;
}
/**
 * 4.0 - Content
 */
html,
body {
  height: auto;
  overflow-x: hidden;
}
.box {
  width: 100%;
}
.container {
  text-align: center;
  margin: 0 auto !important;
}
.note {
  font-weight: bold;
  font-size: 14px;
  text-rendering: optimizeLegibility;
  font-family: 'Cairo', sans-serif;
}
/* 4.1 Menu */
.menu-left {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: fixed;
  width: calc(16.66666667%);
}
.menu-left .face {
  width: 70%;
  height: auto;
  margin-top: 20px;
}
.menu-left .item {
  color: #2b6d82;
  font-variant: small-caps;
  font-size: 32px;
  margin: 7px 0;
  text-align: center;
  cursor: pointer;
  transition: all linear 0.2s;
}
.menu-left .item:hover {
  background-color: #f1f0f0;
}
.menu-left .item.active {
  padding: 45px 0;
}
.menu-top .item {
  color: #2b6d82;
  font-variant: small-caps;
  font-size: 32px;
  cursor: pointer;
}
.menu-top .item:hover {
  background-color: #f1f0f0;
}
/* 4.2 - Home */
.message {
  margin-top: 20px;
}
.message .text {
  text-align: left;
  border-radius: 10px;
  padding: 10px;
}
.message .textarea {
  background: transparent;
  color: white;
  display: inline-block;
  min-height: 45px;
  width: 100%;
  max-height: 300px !important;
  outline: none;
  border: 0px solid #ff6666;
}
.message .textarea:empty:not(:focus):before {
  content: attr(data-text);
}
.message .me {
  color: #4b4f56;
  background-color: #f1f0f0;
}
.message .you {
  color: white;
  background-color: #9acede;
}
.author {
  height: 45px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.author .face {
  width: 45px;
  height: auto;
  display: border-box;
  cursor: pointer;
}
.author span {
  line-height: 45px;
  color: #222;
  font-style: italic;
  font-size: 15px;
  padding: 0 10px;
}
/* 4.3 - About */
.cv {
  background-color: #222;
  padding: 4px 6px;
  width: 100%;
  cursor: pointer;
}
.cv a {
  color: white;
  border: none;
  font-style: normal;
}
.cv:hover {
  background-color: #234452;
}
/* 4.4 - Skills */
.skills {
  list-style-type: none;
  margin: 0;
  padding: 0;
  text-align: left;
}
.skills.full-page {
  text-align: center;
}
.skills.full-page li {
  width: 50%;
  float: left;
}
.skills li {
  padding: 5px 0px 5px 15px;
  margin: 5px 0;
  transition: all linear 0.2s;
}
.skills li:hover {
  background-color: #2b6d82;
  color: white;
}
/* 4.7 - Hobbies */
.hobby {
  width: 75%;
  margin: 45px auto;
}
.hobby .expand {
  width: 100%;
  height: 20px;
  position: relative;
  cursor: pointer;
}
.hobby .expand:before {
  content: "";
  position: absolute;
  top: 0;
  left: calc(47.5%);
  width: 20px;
  height: 20px;
  background-image: url('/img/icon/arrow.svg');
  background-repeat: no-repeat;
  background-position: center bottom;
  background-size: 20px 20px;
}
.hobby p {
  padding: 10px;
  border-left: 1px solid #2b6d82;
}
/* 4.8 - Contact */
.select-me {
  border: none;
  outline: none;
  width: 100%;
}
.contact {
  width: 90%;
  margin: auto;
}
.contact textarea {
  resize: x;
  width: 100%;
  height: 140px;
  border: 1px solid #2b6d82;
  padding: 7px;
}
.contact input {
  width: 100%;
  outline: none;
  border: 1px solid #2b6d82;
  padding: 7px;
  margin: 10px 0;
}
.contact input[type='submit'] {
  color: white;
  background-color: #234452;
  border: none;
}
/**
 * 5.0 - Mixins
 */
.transform:before {
  transform: rotate(-180deg);
}

